
import {mapState, mapActions} from 'vuex'
import helper from '@/mixins/helper'
import tool from '@/utils/tool'
import TabbarBottom from '@/components/TabbarBottom'
import ShareKL from '@/components/ShareKL'
import ShareImg from '@/components/ShareImg'
import Scroll from '@/components/Scroll'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
name: "friends-circle",
components: {
TabbarBottom,
XDialog,
ShareKL,
ShareImg,
Scroll
},
mixins: [helper],
data() {
return {
showShareModal: false,
showKLDialog: false,
showImgDialog: false,
pullUpLoad: {

},
pullDownRefresh: {

},
dataArr: [1],
showTabBar: false
}
},
computed: {
...mapState({

})
},
watch: {

},
mounted() {

},
activated() {
this.showTabBar = true
},
beforeRouteLeave(to, from, next) {
this.showTabBar = false
next()
},
methods: {
...mapActions({}),

handleCopyText(text) {
this.$copyText(text).then((e) => {
this.$vux.toast.text('复制成功', 'middle')
}, (e) => {
console.log(e)
this.$vux.toast.text('复制失败', 'middle')
})
},

handlePulldown() {
setTimeout(()=>{
this.dataArr = [1,2,3]
},3000)
},

handlePullUp() {
setTimeout(()=>{
this.dataArr = new Array(4)
},3000)
}
}
}



